.dropdownContainer {
    margin: 20px 20px;
    width: 300px;
}

.label {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
    color: #5ede99;
}

.dropdown {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
    font-size: 16px;
}
